<template lang="html">
  <div class="row">
    <div class="card mx-auto">
      <div class="card-header">
        <h4>Register Company</h4>
      </div>
      <div class="card-body">
        <form @submit.prevent="registerCompanyThis">
          <div class="form-group">
            <label for="name">Naziv Kompanije</label>
            <input
              class="form-control"
              type="text"
              placeholder="name"
              name="name"
              v-model="name"
              id="name"
              >
          </div>

          <button class="btn btn-primary">Register</button>



        </form>

      </div>
    </div>
  </div>

  <div class="row">
    <div class="card mx-auto">
      <div class="card-header">
        <h4>Register Sector</h4>
      </div>
      <div class="card-body">
        <form @submit.prevent="registerSectorThis">

          <div class="form-group">
            <label for="name">Naziv Sektora</label>
            <input
              class="form-control"
              type="text"
              placeholder="sektor"
              name="sektor"
              v-model="sektor"
              id="sektor"
              >
          </div>

          <div class="form-group" >
            <label for="companys">Member of company</label>
            <select name="type" id="type" v-model="type" >
               <option  v-for="company in companys" v-bind:key="company.name">{{company.name}}</option>
            </select>
          </div>

          <button class="btn btn-primary">Register</button>

        </form>

      </div>
    </div>
  </div>


  <div class="row">
    <div class="card mx-auto">
      <div class="card-header">
        <h4>Register Radno Mesto</h4>
      </div>
      <div class="card-body">
        <form @submit.prevent="registerRadnoMestoThis">

          <div class="form-group">
            <label for="name">Naziv radnog mesta</label>
            <input
              class="form-control"
              type="text"
              placeholder="radnoMesto"
              name="radnoMesto"
              v-model="radnoMesto"
              id="radnoMesto"
              >
          </div>

          <div class="form-group" >
            <label for="companyRM">Company: </label>
            <select name="type" id="companyRM" v-model="companyRM" @change="onChange($event)">
               <option  v-for="company in companys" v-bind:key="company.name">{{company.name}}</option>
            </select>
            <label for="sectorRM">Sector: </label>
            <select name="type" id="sectorRM" v-model="sectorRM" >
               <option  v-for="sek in sektori" v-bind:key="sek">{{sek}}</option>
            </select>
          </div>

          <button class="btn btn-primary">Register</button>

        </form>

      </div>
    </div>
  </div>

</template>

<script>
import {mapActions,mapGetters} from 'vuex';
export default {
  computed: mapGetters(['companys']),
  data(){
    return{
      name:"",
      sektor:"",
      type:"",
      sektori:[],
      companyRM:"",
      sectorRM:"",
      radnoMesto:"",
    }
  },
  methods:{
    ...mapActions(['registerCompany','getCompanies','registerSector','registerRadnoMesto']),
    registerCompanyThis(){
      // eslint-disable-next-line no-unused-vars

      let company = {
        name:this.name,
      }
      this.registerCompany(company);
      this.getCompanies();
    },
    registerSectorThis(){

      let sectorData = {
        companyName:this.type,
        sectorName:this.sektor
      }
      this.registerSector(sectorData);
      this.getCompanies();

    },
    registerRadnoMestoThis(){

      let rMData={
        companyName:this.companyRM ,
        sectorName: this.sectorRM,
        rMName: this.radnoMesto
      }

      this.registerRadnoMesto(rMData);
      this.getCompanies();


    },
    onChange(event) {

        this.sektori = [];

        let tempCompany;
        for (var i = 0; i < this.companys.length; i++) {
          if(this.companys[i].name==event.target.value){
            tempCompany=this.companys[i];
          }
        }
        for ( i = 0; i < tempCompany.sektori.length; i++) {
          this.sektori.push(tempCompany.sektori[i].name);
        }

    }

  },
  created(){
    this.getCompanies();
  }
}
</script>

<style lang="css" scoped>
.row{

  padding-top: 5%;
  text-align: center;
  width:100%;


}
.card{
  width: 60%;
  border-radius: 0;
}
.btn{
  width: 60%;
  background-color: #003366;
  color: white;
  border-radius: 10px;
}
.form-group{
padding-top: 10px;
}
.form-control{
  border-radius: 0;
}
.card-header{
  background-color: #003366;
  color: white;
  border-radius: 10px;
}
</style>
